<template>
  <div id="hello-page">
      <h3>Say Hello 👋</h3>
      <form
       @submit.prevent="handlerSubmit"
        >
          <label for="mail">Mail</label><label style="color:red">*</label><br>
          <input type="email" v-model="email" id="mail" required> <br>
          <textarea v-model="comment" name="" id="" placeholder="Comment"></textarea> <br>
          <button class="btn btn-lg" type="submit">Send</button>
          <label>{{status}}</label>
      </form>
  </div>
</template>

<script>
import axios from "axios"
export default {
    data () {
    return {
      email: null,
      comment : null,
      status : null,

    }
  },
  methods : {
    async handlerSubmit() {
      let data = {
        post: {
        email: this.email,
        comment : this.comment
  },
   };
  console.log(data);
  await this.post(data);
  },
  async post(req) {
      let res = await axios.post(
        'https://hale-lantern-342911-default-rtdb.firebaseio.com/.json',
        req
      );
      console.log(res);
      if (res.status == 200) {
          this.email = "",
          this.comment = "";
          alert("Successfully Send✅")
            
      } else {
        alert("Error! ❌")
      }
    }
  }
}
</script>

<style>
textarea{
    max-height: 150px;
    width: 253px;
}
label{
    font-size: 18px;
}
#mail{
    margin-bottom: 20px;
}
input, textarea {
    border-radius: 5px;
    border: 2px solid rgba(150, 148, 148, 0.827); ;
}
.btn{
    margin-bottom: 60px;
    width: 100px;
    float: left;
    background-image: linear-gradient(180deg,#FFF9F4);
}
.btn:hover{
    box-shadow: rgba(0, 0, 0, 0.10) 0px 3px 8px;
}
textarea::placeholder{
    font-size: 18px;
}
h3{
    margin-bottom: 40px;
}
@media screen and (min-width: 768px){
  .btn{
    margin-bottom: 0px;
  }
  #hello-page{
    justify-content: center;
    display: grid;
}
}
</style>


