    <div class="container">
      <div class="row no-gutters">
        <div class="col-12 col-sm-6 col-md-6 center" data-aos="fade-up">
          <div class="img-area">
        <img src="../assets/bne.jpeg" height="380" class="img-me">
        </div>
        </div>
         <p class="about-me col-6 col-md-4" data-aos="fade-up">Student at Beykent University.
            Department is Computer Engineering. 
            Also second major is Software Engineering. 
            Experienced with front-end development 
            for a dynamic web project. Graduate in 2023.
        </p>
    </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Home',
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#home{
  background-color: #fff;
}
.name{
   color: #fdc89f;  /*#aaa9a9; */
  font-size: 50px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 0px;
}
.social-area{
  display: flex;
  justify-content: center;
  text-align: center;
}
.social-media{
  width: 24px;
}
.social{
