  display: inline-block;
  margin: 10px;
  transition: transform 1s;
}
.social:hover{
  transform: rotate(45deg);
}
.about-me{
  font-size: 24px;
  width: 580px;
  padding-top: 80px;
  padding-bottom: 80px;
  margin: 0 auto;
  padding-left:0x;
}
.img-me{
