  margin: 0 auto;
  border-radius: 100%;
  width: 24em;
  object-fit: cover;
}
.img-area{
  background-image: url("../assets/gif.gif");
}
@media screen and (max-width: 736px){
  .img-me{
    width: 14em;
    height: 14em;
  }
  .about-me{
    font-size: 20px;
  }
}
.container{
  margin-top: 50px;
}
.img-area{
  display: inline-block;
  padding: 1em;
  border-radius: 100%;
  border: solid 1px #dddddd;
  position: relative;
  align-items: center;
}
.center{
  display:flex;
  text-align: center;
  justify-content: center;
}
.row{
  display:flex;
  text-align: center;
  justify-content: center;
}
</style>



