                    <div class="project"><img src="../assets/project.png" alt=""></div>
                    <h4><b>How To Dynamically Render Your Medium Blog Posts On Your Web Site?</b></h4>
                    <p class="description">In this Vue project, I will explain how you can dynamically render the posts you share on the medium to your website. First of all, you need to get your</p>
                    <p class="read"><a href="https://medium.com/@isikduygu/how-to-dynamically-render-your-medium-blog-posts-on-your-web-site-105ed024504f" target="_blank" >Read On Medium</a></p>
                    <p>Feb 14</p>
                </div>
            </div>
          </div>
      </div>
