<style>
#blog{
    background-image: linear-gradient(#F2F2FF, #FFF9F4,#F3FAFA);
}
.title{
    color:#C996CC;
    margin: 50px;
    margin-top:0px;
    font-size: 40px;
    padding-top: 50px;
}
.title:after{
    background-color: #C996CC;
    display: block;
