  letter-spacing: 0;
  line-height: 24px;
}
.description figure{
  display: none
}
.blog-area{
    margin-top:80px;
    background-color: white;
    border-radius: 20px;
    margin: 0 auto;
    padding: 30px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    border-top: 15px solid #e89d1c7c;
}
.read{
