<template>
<div class="SocialMedia">
   <div class="social-area">
    <a href="https://www.linkedin.com/in/duyguisikk/" class="social"><img src="../assets/linkedin.svg" alt="linkedin" class="social-media"></a>
    <a href="https://github.com/dduyguu" class="social"><img src="../assets/github-logo.svg" alt="github" class="social-media"></a>
    <a href="https://medium.com/@isikduygu" class="social"><img src="../assets/medium.svg" alt="medium" class="social-media"></a>
    </div>
</div>
</template>

<script>
export default {
  name: 'Social',
  }
</script>
<style scoped>
.social-area{
  display: flex;
  justify-content: center;
  text-align: center;
  margin-bottom: 50px;
}
.social-media{
  width: 24px;
}
.social{
  height: 50px;
  width: 50px;
   background-image: linear-gradient(180deg,#FFF9F4);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  margin: 2px;
  transition: transform 1s;
}
.social:hover{
  transform: rotate(45deg);
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
</style>


